import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import CIcon from "@coreui/icons-react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import MaterialTable from "material-table";
import Select from "react-select";
import makeAnimated from "react-select/animated";
import {
  GetUsersKecAPIAction,
  setFormUserKecAction,
  AddUserAPIKecAction,
  SetUserInActiveAction,
  SetUserActiveAction,
  GetUserAPIByIdUserAction,
  UpdateUserAPIAction,
  setDataDropdownKabupatenAction,
  setDataDropdownKecamatanAction,
  setDataComboProv,
  SetDropdownJabatanAction,
  SetDropdownJabatan1Action,
  SetUserInActiveKecAction,
  SetUserActiveKecAction,
} from "src/common/redux/action";
import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CInput,
  CLabel,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow,
  CTextarea,
  CSelect,
} from "@coreui/react";
const PegawaiKecamatan = () => {
  /**
   * set management redux
   */
  const {
    dataComboJenisLokpri,
    dataComboProv,
    dataComboKabupaten,
    dataComboKecamatan,
    dataCmbPgwKecamatansByKecamatanReducer,
    dataCmbPgwKecamatansByJabatanReducer,
    dataCmbJabatanReducer,
    dataCmbJabatanReducer1,
  } = useSelector((state) => state.dropdownReducer);
  const { GetDataUsers, modalUserKec, formUserKecReduce } = useSelector(
    (state) => state.pegawaiKecamatanReducer
  );
  const dispatch = useDispatch();
  const [stateUpdate, setStateUpdate] = useState(false);
  const allLoadAction = () => {
    dispatch(GetUsersKecAPIAction("GET_USER_LIST"));
    dispatch(setDataComboProv("DATA_CMB_PROV"));
    dispatch(SetDropdownJabatan1Action("DATA_CMB_JABATAN1"));
  };
  const animatedComponents = makeAnimated();

  const {
    IdUser,
    Username,
    Password,
    Email,
    FirstName,
    MiddleName,
    LastName,
    Address,
    PhoneNumber,
    MobileNumber,
    FileImage,
    Roles,
    IdKecamatan,
    IdJabatan
  } = formUserKecReduce;

  const provinsiChangeHandler = (event) => {
    let idProv = event.target.value;
    dispatch(setDataDropdownKabupatenAction("DATA_CMB_KAB", idProv));
  };

  const kabupatenChangeHandler = (event) => {
    let idKab = event.target.value;
    dispatch(setDataDropdownKecamatanAction("DATA_CMB_KEC", idKab));
  };

  const kecamatanChangeHandler = (event) => {
    let idKec = event.target.value;
      dispatch(setFormUserKecAction("IdKecamatan", idKec));
  };

  const JabatanChangeHandler = (e) => {
    let idJabatan = e.value;
  };

  useEffect(() => {
    allLoadAction();
  }, [dispatch]);
  const showModal = () =>
    dispatch({ type: "MODAL_USER_KEC", payload: !modalUserKec });

  const handleModalUser = (type, idUser) => {
    if (type === "add") {
      dispatch({ type: "RESET_FORM_USER" });
      showModal();
      setStateUpdate(false);
    } else {
      setStateUpdate(true);
      dispatch(GetUserAPIByIdUserAction(idUser));
      showModal();
    }
  };

  const handleAktiveAndNonAktive = (idUser, type, fullName) => {
    if (type === "NoActive") {
      dispatch(SetUserInActiveKecAction(idUser, fullName));
    } else {
      dispatch(SetUserActiveKecAction(idUser, fullName));
    }
  };

  const prosesuserAdd = () => {
    dispatch(AddUserAPIKecAction(formUserKecReduce));
  };

  const prosesUserEdit = () => dispatch(UpdateUserAPIAction(formUserKecReduce));

  const onImageChange = (event) => {
    const filePatch = event.target.files[0];
    if (filePatch !== undefined) {
      dispatch(setFormUserKecAction("FileImage", event.target.files[0].name));
    }
  };

  const columns = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row d-flex justify-content-center">
            <button
              type="button"
              className="btn btn-sm btn-dark text-center mr-2"
              onClick={() => handleModalUser("edit", rowData.idUser)}
            >
              <CIcon name="cil-pencil" /> Update
            </button>
            {/* cek user active or not */}
            {rowData.strStatus === "Aktif" ? (
              <button
                className="btn btn-warning ml-1"
                onClick={() =>
                  handleAktiveAndNonAktive(
                    rowData.idUser,
                    "NoActive",
                    rowData.fullName
                  )
                }
              >
                {" "}
                Non Aktifkan
              </button>
            ) : (
              <button
                className="btn btn-warning ml-1"
                onClick={() =>
                  handleAktiveAndNonAktive(
                    rowData.idUser,
                    "Active",
                    rowData.fullName
                  )
                }
              >
                {" "}
                Aktifkan
              </button>
            )}
          </div>
        );
      },
    },
    { title: "Nama Lengkap", field: "fullName" },
    { title: "Alamat", field: "address" },
    { title: "No Telepone", field: "phoneNumber" },
    { title: "No Handphone", field: "mobileNumber" },
    {
      title: "Status",
      field: "strStatus",
      render: (rowData) =>
        rowData.strStatus === "Aktif" ? (
          <span className="badge badge-primary">{rowData.strStatus}</span>
        ) : (
          <span className="badge badge-dark">{rowData.strStatus}</span>
        ),
    },
  ];
  return (
    <div>
      <CRow>
        <CCol md="12" xl="12" xs="12">
          <CCard>
            <CCardHeader>
              <CCol md="3">
                <CButton
                  block
                  color="primary"
                  onClick={() => handleModalUser("add")}
                >
                  <FontAwesomeIcon icon="plus" /> Tambah{" "}
                </CButton>
              </CCol>
            </CCardHeader>
            <CCardBody>
              <MaterialTable
                title=""
                option={{
                  headerStyle: {
                    backgroundColor: "#01579b",
                    color: "#FFF",
                  },
                  filtering: true,
                }}
                columns={columns}
                data={GetDataUsers.length > 0 ? GetDataUsers : []}
              />
            </CCardBody>
          </CCard>
        </CCol>

        {/* set modal user */}
        <CModal show={modalUserKec} onClose={showModal} size="lg">
          <CModalHeader closeButton>
            <CModalTitle>Form Pengguna</CModalTitle>
          </CModalHeader>
          <CModalBody>
            <CForm className="form-horizontal">
              <CFormGroup row>
                <CCol md="2">
                  <CLabel htmlFor="selectSm">Provinsi</CLabel>
                </CCol>
                <CCol xs="12" md="5">
                  <CSelect
                    name="provinsi"
                    id="provinsi"
                    onChange={provinsiChangeHandler}
                  >
                    <option value="0"> --Pilih Provinsi -- </option>
                    {dataComboProv
                      ? dataComboProv.map((val, index) => (
                          <option key={val.idProvinsi} value={val.idProvinsi}>
                            {val.nama}
                          </option>
                        ))
                      : null}
                  </CSelect>
                </CCol>
              </CFormGroup>

              <CFormGroup row>
                <CCol md="2">
                  <CLabel htmlFor="selectSm">Kabupaten</CLabel>
                </CCol>
                <CCol xs="12" md="5">
                  <CSelect
                    name="kabupaten"
                    id="kabupaten"
                    onChange={kabupatenChangeHandler}
                  >
                    <option value="0"> --Pilih Kabupaten -- </option>
                    {dataComboKabupaten.length > 0
                      ? dataComboKabupaten.map((val, idx) => (
                          <option key={val.idKabupaten} value={val.idKabupaten}>
                            {val.nama}
                          </option>
                        ))
                      : null}
                  </CSelect>
                </CCol>
              </CFormGroup>

              <CFormGroup row>
                <CCol md="2">
                  <CLabel htmlFor="selectSm">Kecamatan</CLabel>
                </CCol>
                <CCol xs="12" md="5">
                  <CSelect
                    name="kecamatan"
                    id="kecamatan"
                    onChange={(event) => kecamatanChangeHandler(event)}
                  >
                    <option value="0"> --Pilih Kecamatan -- </option>
                    {dataComboKecamatan.map((val, idx) => (
                      <option key={val.idKecamatan} value={val.idKecamatan}>
                        {" "}
                        {val.nama}
                      </option>
                    ))}
                  </CSelect>
                </CCol>
              </CFormGroup>
              <CFormGroup row>
                <CCol md="2">
                  <CLabel htmlFor="pilihJabatan"> Pilih Jabatan</CLabel>
                </CCol>
                <CCol xs="12" md="5">
                  <CSelect
                    name="provinsi"
                    id="provinsi"
                    onChange={(event) =>
                      dispatch(
                        setFormUserKecAction("IdJabatan", event.target.value)
                      )
                    }
                  >
                    <option value="0"> --Pilih Jabatan -- </option>
                    {console.log(dataCmbJabatanReducer1)}
                    {dataCmbJabatanReducer1
                      ? dataCmbJabatanReducer1.map((val, index) => (
                          <option key={val.idJabatan} value={val.idJabatan}>
                            {val.nama}
                          </option>
                        ))
                      : null}
                  </CSelect>
                </CCol>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-username" className="col-md-2">
                  Username
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="text"
                    placeholder="Input Username ..."
                    value={Username || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserKecAction("Username", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup
                className="form-row"
                style={{ display: stateUpdate ? "none" : "" }}
              >
                <CLabel htmlFor="nf-password" className="col-md-2">
                  Password
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="password"
                    placeholder="Input Password ..."
                    value={Password || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserKecAction("Password", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-email" className="col-md-2">
                  Email
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="email"
                    placeholder="Input Email ..."
                    value={Email || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserKecAction("Email", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-fullname" className="col-md-2">
                  Nama Lengkap
                </CLabel>
                <div className="col-md-3">
                  <CInput
                    type="text"
                    placeholder="Input nama depan ..."
                    value={FirstName || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserKecAction("FirstName", event.target.value)
                      )
                    }
                  />
                </div>
                <div className="col-md-3">
                  <CInput
                    type="text"
                    placeholder="Input nama tengah ..."
                    value={MiddleName || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserKecAction("MiddleName", event.target.value)
                      )
                    }
                  />
                </div>
                <div className="col-md-3">
                  <CInput
                    type="text"
                    placeholder="Input nama akhir ..."
                    value={LastName || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserKecAction("LastName", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-address" className="col-md-2">
                  Alamat
                </CLabel>
                <div className="col-md-9">
                  <CTextarea
                    placeholder="Input Alamat"
                    value={Address || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserKecAction("Address", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-notelepon" className="col-md-2">
                  No Telepon
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="text"
                    placeholder="Input no telepon ..."
                    value={PhoneNumber || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserKecAction("PhoneNumber", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-mobilenumber" className="col-md-2">
                  No Handphone
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="text"
                    placeholder="Input no hp ..."
                    value={MobileNumber || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserKecAction("MobileNumber", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-mobilenumber" className="col-md-2">
                  Upload Foto
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="file"
                    onChange={(e) => onImageChange(e)}
                    placeholder="Input no hp ..."
                  />
                </div>
              </CFormGroup>
            </CForm>
          </CModalBody>
          <CModalFooter>
            <CButton
              color="primary"
              onClick={stateUpdate ? prosesUserEdit : prosesuserAdd}
            >
              {stateUpdate ? "Update" : "Tambah"}
            </CButton>
            <CButton color="secondary" onClick={showModal}>
              Cancel
            </CButton>
          </CModalFooter>
        </CModal>
      </CRow>
    </div>
  );
};

export default PegawaiKecamatan;
